<template>
  <div id="dialog-quantity">
    <v-btn flat icon outline small @click="decrease">
      <v-icon small>remove</v-icon>
    </v-btn>

    <v-text-field hide-details class="pa-0 text-center"
      v-model="quantity"
      @change="validateQuantity"
      :mask="mask" />

    <v-btn flat icon outline small @click="increase">
      <v-icon small>add</v-icon>
    </v-btn>
  </div>
</template>

<script>
export default {
  computed: {
    isMyx() {
      return this.$store.state.cart.activeStallUid === 'myx';
    },
    maxValue() {
      if (this.isMyx) return 1;
      else return 99;
    },
    mask() {
      if (this.isMyx) return '#';
      else return '##';
    },

    item() { return this.$store.state.orders.currentItem },
    quantity: {
      get() {
        if (this.item === null) return 1;
        else return this.item.quantity;
      },
      set(value) {
        value = parseInt(value);
        value = Math.min(value, this.maxValue);

        if (this.item !== null)
          this.item.quantity = value;
      },
    },
  },

  methods: {
    validateQuantity() {
      if (this.quantity > this.maxValue) this.quantity = this.maxValue;
    },
    valid(quantity) {
      if (typeof this.quantity !== 'number') return false;
      if (isNaN(this.quantity)) return false;
      return true;
    },
    increase() {
      if (!this.valid(this.quantity)) this.quantity = 1;
      if (this.quantity < this.maxValue) this.quantity += 1;
    },
    decrease() {
      if (!this.valid(this.quantity)) this.quantity = 1;
      if (this.quantity > 0) this.quantity -= 1;
    },
  },
};
</script>

<style>
.v-input input {
  text-align: center;
  font-weight: bold;
}
</style>

<style lang="scss" scoped>
@import '@/assets/css/main.scss';

#dialog-quantity {
  width: 40%;

  display: flex;
  flex-direction: row;
}

.v-btn--icon.v-btn--outline {
  border-radius: 5px;
  border-width: 2px;
  border-color: $primary-color;
}
</style>
