import Vue from 'vue';
import Vuex from 'vuex';
import orders from './modules/orders';
import cart from './modules/cart';
import api from './modules/api';
import payment from './modules/payment';

Vue.use(Vuex);

export default new Vuex.Store({
  modules: {
    orders,
    cart,
    api,
    payment,
  },

  state: {
    menuOpen: false,
    cartOpen: false,
    paymentOpen: false,
    toolbarOpen: false,

    activeStallUid: null,
    activeReceiptId: null,

    snackbar: false,

    customer: {
      id: -1,
      email: '',
      type: 'school',
    },
  },

  getters: {
    isLoggedIn(state) {
      return state.customer.id >= 0;
    },
    activeMenu(state) {
      let uid = state.activeStallUid;
      return state.api.menu[uid];
    },

    canOrder(state, getters) {
      let emails = new Set([
        'myx.brewery@gmail.com',
        'chanpinghui@gmail.com',
        'uniquosity@gmail.com',
        'yustynn@gmail.com',
        'gabrielwong159@gmail.com',
        'godkayy@gmail.com',
        'yos.yohannes@gmail.com',
      ]);

      if (emails.has(state.customer.email))
        return true;

      if (state.cart.activeStallUid !== 'myx') return true;
      if (state.api.orders.filter(order => order.status_id >= 0).length > 0)
        return false;
      if (state.cart.cart.myx && state.cart.cart.myx.length > 0)
        return false;
      return true;
    },
  },

  mutations: {
    login(state, { id, email }) {
      state.customer = {
        ...state.customer,
        id,
        email,
      };
    },
    logout(state) {
      state.customer = {
        ...state.customer,
        id: -1,
        email: '',
      };
    },

    toggleMenu(state) {
      state.menuOpen = !state.menuOpen;
    },

    openPayment(state) {
      state.paymentOpen = true;
    },
    closePayment(state) {
      state.paymentOpen = false;
    },

    closeCart(state) {
      state.cartOpen = false;
    },

    setActiveStall(state, uid) {
      state.activeStallUid = uid;
      state.cart.activeStallUid = uid;
    },
  },

  actions: {
    openPayment({ commit }) {
      commit('toggleMenu');
      commit('closeCart');

      commit('payment/initPayment');
      commit('openPayment');
    },

    notification({ state }, receiptId) {
      state.activeReceiptId = receiptId;
      state.snackbar = true;
    },
  },
});
