<template>
  <v-list-group :value="isActive">
    <template v-slot:activator>
      <order-list-item-title :order="order"/>
    </template>

    <order-list-item-card :order="order"/>
  </v-list-group>
</template>

<script>
import OrderListItemTitle from './OrderListItemTitle.vue';
import OrderListItemCard from './OrderListItemCard.vue';

export default {
  props: ['order'],

  components: {
    'order-list-item-title': OrderListItemTitle,
    'order-list-item-card': OrderListItemCard,
  },

  computed: {
    isActive() {
      let activeId = this.$store.state.activeReceiptId;
      return this.order.id === activeId;
    },
  },
};
</script>
